import React from "react";
import './AboutMe.css';
import avatar from "../../images/film_Avatar_1304.jpg";
import Portfolio from "../Portfolio/Portfolio";

const AboutMe = () => {
    return (
        <section className="about-me">
            <h3 className="main__section-title"></h3>
            <div className="about-me__content">
                <div className="about-me__info">
                    <h3 className="about-me__title"></h3>
                    <p className="about-me__subtitle">-, 32 </p>
                    <p className="about-me__description">
                             ,     .
                             .
                               .
                         2019      .
                         ,     -      
                          .
                    </p>
                    <ul className="about-me__links">
                        <li><a className="about-me__link" href="src/components/Main/Main.js" target="_blank"
                               rel="noreferrer">Facebook</a></li>
                        <li><a className="about-me__link" href="src/components/Main/Main.js" target="_blank"
                               rel="noreferrer">Github</a></li>
                    </ul>
                </div>
                <img src={avatar} alt="about-me" className="about-me__avatar"/>
            </div>
            <Portfolio/>
        </section>
    );
}

export default AboutMe;
